@import '../mixins';

.home_mobile {
  display: none;

  @include for-mobile {
    display: block;
  }

  &_presentation {
    margin-top: 5vh;
    transition: 2s ease-in-out;

  }
}

.home {


  @include for-tablet {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10vh;
  }

  @include for-mobile {
    display: flex;
    flex-direction: column;
    align-items: center;
  }



  &_tablet {

    display: none;

    @include for-tablet {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
    }

    @include for-mobile {
      display: none;
    }

    &_collapse {

      @include for-tablet {
        margin-top: 10vh;
        border-radius: 25px;
        display: flex;


        @include box-shadow;
        height: 50px;
        width: 90%;
        background-color: $bg-Text;
      }
    }

    &_footer {
      width: 100%;
      padding: -5vh;
    }
  }

  &_desktop {
    display: flex;
    width: 100%;

    &_aside {

      width: 20%;
    }

    &_main {
      width: 80%;
    }

    @include for-mobile {
      display: none;

    }

    @include for-tablet {
      display: none;
    }

  }
}

.tab_service {

  display: none;


  @include for-tablet {
    width: 30%;
    display: flex;
    align-items: center;
    background: none;
    border: none;
    cursor: pointer;
  }

  @include for-mobile {
    display: none;
  }

  &:hover {
    color: black;
  }

  &.active {

    height: 100%;
    font-weight: bold;
    border-radius: 25px;
    background-color: $blue;
    color: white;

  }

  &_title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.homeDesk {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 0;

  &_text {
    margin-top: 5vh;
    padding: 20px;
    width: 80%;
    font-size: 18px;
    line-height: 1.5;
    height: 100%;
    position: relative;
    background-color: $bg-Text;
    
    border-radius: 25px;
    
    h2{
      width: 100%;
      text-align: center;
      height: 50px;
      font-size: 35px;
      font-weight: bold;
      margin-bottom: 5vh;

    }
    p{
      margin-top: 2vh;
      margin-bottom: 2vh;
      
    }
    &::after,
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(45deg, rgb(0, 0, 70), blue, rgb(0, 183, 255), rgb(0, 0, 70), blue, rgb(0, 183, 255));
      width: 100%;
      height: 100%;
      border-radius: 25px;
      background-size: 500%;
      z-index: -1;
      //transform: scale(1.01);
      animation: animate 20s infinite;

    }
    &::after{
      filter: blur(20px);
    }
  }
  &_citation{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 10vh;
    width: auto;
    padding: 10px;
    border-radius: 25px;
    background-color: $bg-Text;
    @include push;
    img {
      margin-right: 20px;
    }

  }
}



@keyframes animate {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 300% 0;
  }

  100% {
    background-position: 0 0;
  }
}